import React, {memo} from 'react';
import styled from 'styled-components';

const SnsContainer = styled.div`
    background-color: #333333;
    width:100%;
    height: 500px;
    color:#fff;
    padding-top:80px;

    .snsWrap{
        max-width: 1200px;
        margin:0 auto;
        display: flex;
        
        .snsInner{
            padding:30px 5px;

            .title{
                height: max-content;
                margin-bottom:30px;
                line-height: 1.6;

                .link{
                    font-size:28px;
                    color:#fff;
                    font-weight: 600;
                }
            }
        }

        

        .content{
            li{
                font-size:18px;
                color:#fff;
                margin-bottom:10px;

                &::before{
                    content: "";
                    margin-right:5px;
                }
            }
        }
    }
`;

const Sns = memo(() => {
  return (
    <SnsContainer>
        <div className='snsWrap'>
            <div className='snsInner'>
                <h1 className='title'><a href='https://kimhs7858.tistory.com/' target='_blank' className='link'>  (https://kimhs7858.tistory.com/)</a></h1>
                <ol className='content'>
                    <li>        </li>
                    <li>     </li>
                </ol>
            </div>
            <div className='snsInner'>
                <h1 className='title'><a href='https://github.com/Kimhansoool' target='_blank' className='link'> (https://github.com/Kimhansoool)</a></h1>
                <ol className='content'>
                    <li>  </li>
                    <li> , ,  </li>
                </ol>
            </div>
        </div>
    </SnsContainer>
  );
});

export default Sns;
